.page {
  width: 100%;
  min-height: 100dvh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
}

/* Анимация появления */
@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

.title {
  text-align: center;
  font-family: '20db', sans-serif;
  font-size: 3.5vw;
  color: white;
  margin-top: 8dvh;
}

/* Общий стиль изображений */
.img {
  position: absolute;
  object-fit: contain;
  aspect-ratio: 1 / 1;
  opacity: 0;
  animation: fadeIn 0.6s ease-out forwards;
}

/* === Позиции (можешь подогнать под себя) === */
.img1 {
  top: 25vh;
  left: 6vw;
  width: 45vw;
}

.img2 {
  top: 10vh;
  right: 6vw;
  width: 40vw;
}

/* Иконка (без анимации) */
.icon {
  top: 10vh;
  left: 90%;
  transform: translateX(-50%);
  width: 8vw;
  opacity: 1;
  animation: none;
}

/* Подпись снизу справа */
.note {
  position: absolute;
  right: 4vw;
  bottom: 4vh;
  font-size: 1.2vw;
  font-family: 'TTAutonomousMonoTrialBold', sans-serif;
  color: white;
  opacity: 0.7;
}

/* === Адаптив для мобильных === */
@media (max-width: 768px) {
  .title {
    font-size: 6vw;
    margin-top: 21dvh;
    padding: 0 20px;
  }

  .img1, .img2 {
    width: 40vw;
    left: 50% !important;
    right: auto !important;
    transform: translateX(-50%);
    position: absolute;
  }

  .img1 { top: 30vh; }
  .img2 { top: 50vh; }

  .icon {
    top: 15vh;
    left: 85%;
    width: 12vw;
  }

  .note {
    font-size: 3.5vw;
    right: 5vw;
    bottom: 5vh;
  }
}