.page {
  width: 100%;
  min-height: 100dvh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
}

.title {
  text-align: center;
  font-family: '20db', sans-serif;
  font-size: 3.5vw;
  color: white;
  margin-top: 8dvh;
}

/* Общее для изображений */
.img {
  position: absolute;
  object-fit: contain;
  opacity: 0;
  animation: fadeIn 0.6s ease-out forwards;
}

/* Анимация появления */
@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

/* Размеры по умолчанию */
.img1, .img2, .img3, .img4, .img5, .img6, .icon1, .icon2 {
  width: 18vw;
  aspect-ratio: 1 / 1;
}

/* Позиции — можно подогнать под себя */
.img1 { top: 35vh; left: 55vw; width: 10dvw; }
.img2 { top: 75vh; left: 23vw; width: 8dvw; }
.img3 { top: 55vh; right: 20vw; }
.img4 { top: 35vh; right: 10vw; }

.img5 { bottom: 25vh; left: 30vw; width: 20dvw; }
.img6 { bottom: 25vh; right: 70vw; }

/* Иконки — без анимации */
.icon1 {
  top: 20vh;
  left: 90vw;
  width: 8vw;
  animation: none;
  opacity: 1;
}
.icon2 {
  top: 12vh;
  right: 90vw;
  width: 8vw;
  animation: none;
  opacity: 1;
}

/* ===== Адаптив ===== */
@media (max-width: 768px) {
  .title {
    font-size: 6vw;
    margin-top: 23dvh;
    text-align: center;
    padding: 0 20px 4vh;
  }

  .img1,
  .img2,
  .img3,
  .img4,
  .img5,
  .img6 {
    position: static;
    width: 42vw;
    margin: 2vw;
    transform: none;
  }

  .page {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  /* Обёртка для 2 колонок */
  .mobile-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 10vh;
  }

  .icon1,
  .icon2 {
    display: none;
  }
}