.page {
  width: 100%;
  min-height: 100dvh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
  padding-top: 80px; /* отступ под фиксированный хедер */
}

.title {
  text-align: center;
  font-family: '20db', sans-serif;
  font-size: 3.5vw;
  color: white;
  margin-top: 2dvh;
}

/* Анимация появления */
@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

/* Общие стили */
.img {
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  object-fit: contain;
  width: 20vw;
  aspect-ratio: 1 / 1;
  opacity: 0;
  animation: fadeIn 0.6s ease-out forwards;
}

/* ==== Основные фото ==== */
.img1 {
  top: 27dvh;
  left: 5vw;
  width: 35dvw;
}
.img2 {
  top: 18dvh;
  right: 35vw;
  width: 25dvw;
}
.img3 {
  bottom: 0.5dvh;
  left: 40vw;
  width: 25dvw;
}
.img4 {
  bottom: 5dvh;
  right: 5vw;
  width: 35dvw;
}

/* ==== Иконки (без анимации) ==== */
.icon1,
.icon2 {
  opacity: 1;
  animation: none;
}

.icon1 {
  top: 17dvh;
  left: 10vw;
  width: 6vw;
}
.icon2 {
  bottom: 70dvh;
  right: 0vw;
  width: 7vw;
}

/* ==== Адаптив для мобильных ==== */
@media (max-width: 768px) {
  .title {
    font-size: 6vw;
    margin-top: 30dvw;
  }

  .img {
    width: 30vw;
    left: 50% !important;
    right: auto !important;
    transform: translateX(-50%);
  }

  .img1 { top: 40dvh; }
  .img2 { top: 54dvh; }
  .img3 { top: 65dvh; }
  .img4 { top: 78dvh; }

  /* Скрываем иконки на мобильных */
  .icon1,
  .icon2 {
    display: none;
  }
}