/* ======== Шрифты ======== */
@font-face {
  font-family: '20db';
  src: url('./assets/fonts/20db.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'AutonomousMonoBold';
  src: url('./assets/fonts/TTAutonomousMonoTrialBold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

/* ======== Сброс и база ======== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root {
  width: 100%;
  height: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden;
  font-family: '20db', sans-serif;
  background: linear-gradient(to bottom right, #001933, #002d59);
  color: white;
}

/* ======== Главный контейнер ======== */
.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* ======== Основной контент ======== */
main {
  flex: 1;
  width: 100%;
  overflow-x: hidden;
}

section {
  width: 100%;
  padding: 0;
  margin: 0;
}

/* ======== Заголовки секций ======== */
h2 {
  font-family: 'AutonomousMonoBold', sans-serif;
  font-size: 38px;
  text-align: center;
  margin-bottom: 40px;
  letter-spacing: 1.5px;
}

/* ======== Адаптив ======== */
@media (max-width: 768px) {
  h2 {
    font-size: 28px;
    padding: 0 20px;
  }

  section {
    padding: 40px 0;
  }
}