.page {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  position: relative;
  padding: 8vh 20px;
  overflow: hidden;
  padding-top: 80px;
}

/* Анимация появления */
@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

/* Заголовки */
.header {
  text-align: center;
  margin-bottom: 5vh;
}

.title {
  font-family: '20db', sans-serif;
  font-size: 3.5vw;
  color: white;
  margin-bottom: 1vh;
}

.subtitle {
  font-family: 'AutonomousMonoBold', sans-serif;
  font-size: 1.6vw;
  color: #fff;
}

/* Обертка для наложения изображений */
.imageWrapper {
  position: relative;
  width: 40vw;
  margin: 0 auto;
  z-index: 2;
}

/* Главное изображение */
.centralImage {
  width: 100%;
  height: auto;
  border-radius: 12%;
  display: block;
  position: relative;
  z-index: 1;
  opacity: 0;
  animation: fadeIn 0.6s ease-out forwards;
}

/* Второе изображение поверх */
.overlayImage {
  position: absolute;
  top: 50%;
  left: 35%;
  width: 20%;
  z-index: 2;
  opacity: 0;
  animation: fadeIn 0.6s ease-out forwards;
  animation-delay: 0.2s;
}

/* Подпись под арт */
.artText {
  text-align: center;
  font-family: 'AutonomousMonoBold', sans-serif;
  font-size: 1.3vw;
  color: white;
  margin-top: 3vh;
  position: relative;
  z-index: 2;

  opacity: 0;
  animation: fadeIn 0.6s ease-out forwards;
  animation-delay: 0.2s;
}

/* Свободно позиционируемые иконки */
.freeImage {
  position: absolute;
  width: 10vw;
  top: 45vh;
  left: 44vw;
  z-index: 3;

  opacity: 0;
  animation: fadeIn 0.6s ease-out forwards;
  animation-delay: 0.4s;
}

.icon1 {
  position: absolute;
  top: 20vh;
  left: 90vw;
  transform: translate(-50%, -50%);
  width: 8vw;
  opacity: 0;
  animation: fadeIn 0.6s ease-out forwards;
  animation-delay: 0.4s;
}

/* ===== Адаптив ===== */
@media (max-width: 768px) {
  .page {
    padding-top: 200px;
  }

  .title {
    font-size: 6vw;
  }

  .subtitle {
    font-size: 4vw;
  }

  .imageWrapper {
    width: 80vw;
  }

  .centralImage {
    width: 100%;
  }

  .overlayImage {
    top: 35%;
    left: 30%;
    width: 25%;
  }

  .artText {
    font-size: 4vw;
  }

  .freeImage {
    width: 20vw;
    top: 33vh;
    left: 50%;
    transform: translateX(-50%);
  }

  .icon1 {
    width: 16vw;
    top: 20vh;
    left: 85vw;
    transform: translate(-50%, -50%);
  }
}