.page {
  width: 100%;
  min-height: 100dvh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  padding-top: 10vh;
}

.title {
  text-align: center;
  font-family: '20db', sans-serif;
  font-size: 3.5vw;
  color: white;
  margin-bottom: 5vh;
}

/* GRID для десктопа */
.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: min-content;
  row-gap: 0.2vh;
  column-gap: 0; /* убираем зазор между колонками */
  width: 100%;
  padding: 0;
}

/* Иконки */
.locationImage {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  aspect-ratio: 2 / 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60%; /* чуть меньше, чтобы не растягивались */
  margin: 0;
}

/* Подписи */
.label {
  font-family: 'AutonomousMonoBold', sans-serif;
  font-size: 1.4vw;
  color: white;
  text-align: center;
  text-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  cursor: pointer;
  padding: 10px;
}

/* Расположение */
.loc5 , .loc3, .loc1{
  justify-self: start;
}
.loc2, .loc4 {
  justify-self: end;
}

/* ===== Адаптив ===== */
@media (max-width: 768px) {
  .title {
    font-size: 6vw;
    margin-top: 6dvh;
    padding: 0 20px;
  }

  .grid {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5vh; /* ближе друг к другу */
    padding: 0;
  }

  .locationImage {
    width: 95vw; /* почти вплотную к краю */
  }

  .label {
    font-size: 5vw;
    padding: 10px;
  }
}