.page {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  padding: 80px 20px;
}

.title {
  text-align: center;
  font-family: '20db', sans-serif;
  font-size: 3.5vw;
  color: white;
  margin-bottom: 8vh;
}

/* Общее для изображений */
.img {
  position: absolute;
  object-fit: contain;
  opacity: 0;
  animation: fadeIn 0.9s ease-out forwards;
}

/* Анимация появления */
@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

/* Размеры по умолчанию */
.img1, .img2, .img3, .icon1, .icon2 {
  width: 18vw;
  aspect-ratio: 1 / 1;
}

/* Позиции изображений */
.img1 {
  top: 50vh;
  left: 64vw;
  width: 30vw;
}
.img2 {
  top: 15vh;
  right: 25vw;
  width: 30vw;
}
.img3 {
  bottom: 10vh;
  left: 5vw;
  width: 32vw;
}

/* Позиции иконок (без анимации) */
.icon1 {
  top: 20vh;
  left: 85vw;
  width: 8vw;
  animation: none;
  opacity: 1;
}
.icon2 {
  top: 70vh;
  right: 45vw;
  width: 15vw;
  animation: none;
  opacity: 1;
}

/* ===== Адаптив ===== */
@media (max-width: 768px) {
  .title {
    font-size: 6vw;
    margin-top: 12dvh;
    padding: 0 20px;
  }

  .img1, .img2, .img3 {
    position: static;
    display: block;
    width: 60vw;
    margin: 0 auto 20px auto;
  }

  .icon1, .icon2 {
    display: none;
  }
}