.page {
  width: 100%;
  height: 100vh;
  position: relative;
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

.title {
  position: absolute;
  top: 200px;
  left: 50%;
  transform: translateX(-50%);
  font-family: '20db', sans-serif;
  font-size: 40px;
  color: white;
  text-align: center;
}

.phone {
  position: absolute;
  top: 300px;
  left: 50%;
  transform: translateX(-50%);
  font-family: 'AutonomousMonoBold', sans-serif;
  font-size: 30px;
  color: white;
  text-align: center;
}

.email {
  position: absolute;
  top: 370px;
  left: 50%;
  transform: translateX(-50%);
  font-family: 'AutonomousMonoBold', sans-serif;
  font-size: 30px;
  color: white;
  text-align: center;
}

.icon {
  position: absolute;
  bottom: 600px;
  right: 100px;
  width: 100px;
}

/* ===== Адаптив ===== */
@media screen and (max-width: 1024px) {
  .title {
    font-size: 32px;
    top: 160px;
  }

  .phone {
    font-size: 24px;
    top: 260px;
  }

  .email {
    font-size: 24px;
    top: 320px;
  }

  .icon {
    width: 80px;
    bottom: 500px;
    right: 80px;
  }
}

@media screen and (max-width: 768px) {
  .title {
    font-size: 26px;
    top: 120px;
  }

  .phone {
    font-size: 20px;
    top: 200px;
  }

  .email {
    font-size: 20px;
    top: 250px;
  }

  .icon {
    width: 60px;
    bottom: 100px;
    right: 40px;
  }
}

@media screen and (max-width: 480px) {
  .title {
    font-size: 20px;
    top: 100px;
  }

  .phone {
    font-size: 16px;
    top: 170px;
  }

  .email {
    font-size: 16px;
    top: 210px;
  }

  .icon {
    width: 50px;
    bottom: 60px;
    right: 20px;
  }
}