.page {
  width: 100%;
  min-height: 100dvh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
}

.title {
  text-align: center;
  font-family: '20db', sans-serif;
  font-size: 3.5vw;
  color: white;
  margin-top: 8dvh;
}

/* Блок со стрелкой и текстом */
.offerBlock {
  width: 30vw;
  aspect-ratio: 2.5 / 1;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 2vw;
}

/* Текст поверх стрелки */
.offerText {
  font-family: 'AutonomousMonoBold', sans-serif;
  font-size: 1.2vw;
  color: white;
  text-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  pointer-events: none;
}

/* ==== Позиции блоков === */
.block1 {
  top: 25vh;
  left: 0vw;
}

.block2 {
  top: 55vh;
  right: 70vw;
}

.block3 {
  bottom: 25vh;
  left: 70vw;
}

.block4 {
  bottom: 55vh;
  right: 0vw;
}

@media (max-width: 768px) {
  .title {
    font-size: 6vw;
    margin-top: 6dvh;
    padding: 0 20px;
  }

  /* Убираем абсолютные позиции на мобилках */
  .block1,
  .block2,
  .block3,
  .block4 {
    position: static;
    transform: none;
    width: 80vw;
  }

  .mobileWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4vh;
    padding: 6vh 0 12vh;
  }

  .offerBlock {
    width: 100%;
    aspect-ratio: 2.5 / 1;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5vw;
  }

  .offerText {
    font-size: 4vw;
    text-align: center;
    line-height: 1.3;
  }
}