.hero {
  position: relative;
  width: 100%;
  height: 100dvh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.centerContent {
  position: absolute;
  top: 43%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
}

.mur {
  font-family: '20db', sans-serif;
  font-size: 11vw;
  color: white;
  text-shadow: 4px 4px 0 #002e80;
  margin: 0;
  position: relative;
  left: 25vw; /* сдвиг "МУР" вправо адаптивно */
}

.subtitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2vh;
  margin-top: 5vh;
}

.word {
  font-family: '20db', sans-serif;
  font-size: 3.6vw;
  color: white;
  margin: 0;
  white-space: nowrap;
}

.outline {
  color: transparent;
  -webkit-text-stroke: 1.5px #ffffff;
}

/* Индивидуальные сдвиги */
.word1 {
  transform: translateX(-12vw); /* МУЗЕЙ по центру */
}
.word2 {
  transform: translateX(-0.5vw); /* УРАЛЬСКОГО немного вправо */
}
.word3 {
  transform: translateX(-7vw); /* РОКА чуть влево */
}

/* === Адаптив для телефонов === */
@media (max-width: 768px) {
  .mur {
    font-size: 18vw;
    left: 0; /* убираем сдвиг для узких экранов */
  }

  .word {
    font-size: 7vw;
  }

  .word1,
  .word2,
  .word3 {
    transform: none;
  }
}
